					color: "#ffffff",
					bgColor: "#9e5fff",
					dragBgColor: "#9e5fff",
					borderColor: "#9e5fff"
				},
				{
					id: "2",
					name: "Company",
					color: "#00a9ff",
					bgColor: "#00a9ff",
					dragBgColor: "#00a9ff",
					borderColor: "#00a9ff"
				}
			],
			schedules: [
